import React, { useState } from "react";
import {
  WrapperStyle,
  ColumnBox,
  CreateBoxstyle,
  Shadowstyle,
  LeftBox,
  RightTopBox,
} from "./style";
import SideBar from "../../components/SideBar";
import Selectimg from "../../assets/images/file.jpg";
import "antd/dist/antd.css";
import { Select } from "antd";

function CreatePage() {
  const { Option } = Select;

  function handleChange(value) {
    console.log(`selected ${value}`);
  }

  const [turning, setturning] = useState(false);

  const handlerclick = (e) => {
    setturning(true);
    console.log("working");
    console.log(e);
  };

  return (
    <WrapperStyle>
      <SideBar />
      <ColumnBox>
        <CreateBoxstyle>
          <h2>Create a new product</h2>
          <ul>
            <li style={{ listStyle: "none", marginLeft: "0" }}>Dashboard</li>
            <li>E-Commerce</li>
            <li style={{ color: "#aaa" }}>New product</li>
          </ul>
          <div
            style={{
              display: "flex",
              justifyContent: "space-between",
              marginTop: "40px",
              marginLEft: "auto"
            }}
          >
            <Shadowstyle style={{ width: "62%" }}>
              <LeftBox>
                <label htmlFor="">Name</label>
                <input type="text" name="name" placeholder="Product Name" />
                <label htmlFor="">Description</label>
                <textarea
                  name="description"
                  placeholder="Write something awesome..."
                ></textarea>
                <p>Add images</p>
                <div className="selectimg">
                  <img src={Selectimg} alt="" />
                  <div>
                    <h2>
                      <b>Drop or Select file</b>
                    </h2>
                    <h3>
                      Drop files here or click to
                      <a
                        href="https://www.google.com"
                        target="_blank"
                        style={{ textDecoration: "underline" }}
                      >
                        browse
                      </a>
                      through your machine
                    </h3>
                  </div>
                </div>
              </LeftBox>
            </Shadowstyle>
            <div
              style={{ display: "flex", flexDirection: "column", width: "34%" }}
            >
              <RightTopBox>
                <p className="title">
                  <div
                    onClick={() => handlerclick}
                    state={turning}
                    className="turning"
                  ></div>
                  in stock
                </p>
                <input
                  className="textinput"
                  type="text"
                  placeholder="Prduct Code"
                />
                <input
                  className="textinput"
                  type="text"
                  placeholder="Prduct SKU"
                />
                <label htmlFor="gender">Gender</label>
                <div className="radios">
                  <div>
                    <input type="radio" name="gender" />
                    Men
                  </div>
                  <div>
                    <input type="radio" name="gender" />
                    Women
                  </div>
                  <div>
                    <input type="radio" name="gender" />
                    Kids
                  </div>
                </div>
                <label htmlFor="category"></label>
                <Select
                  className="select"
                  name="category"
                  showSearch
                  style={{ width: "100%" }}
                  placeholder="Category"
                  optionFilterProp="children"
                  filterOption={(input, option) =>
                    option.children
                      .toLowerCase()
                      .indexOf(input.toLowerCase()) >= 0
                  }
                  filterSort={(optionA, optionB) =>
                    optionA.children
                      .toLowerCase()
                      .localeCompare(optionB.children.toLowerCase())
                  }
                >
                  <Option value="1">shorts</Option>
                  <Option value="2">T-shirts</Option>
                  <Option value="3">Shoes</Option>
                </Select>
                <label htmlFor="tags">Tags</label>
                <Select
                  className="select"
                  name="tags"
                  mode="multiple"
                  style={{ width: "100%" }}
                  placeholder="Tags"
                  defaultValue={["china"]}
                  onChange={handleChange}
                  optionLabelProp="label"
                >
                  <Option value="china" label="Toy story">
                    <div className="demo-option-label-item">Toy story</div>
                  </Option>
                  <Option value="usa" label="Toy story 2">
                    <div className="demo-option-label-item">Toy story 2</div>
                  </Option>
                  <Option value="japan" label="Toy story 3">
                    <div className="demo-option-label-item">Toy story 3</div>
                  </Option>
                </Select>
              </RightTopBox>
            </div>
          </div>
        </CreateBoxstyle>
      </ColumnBox>
    </WrapperStyle>
  );
}

export default CreatePage;
