import styled from "styled-components";

export const WrapperStyle = styled.div`
  display: flex;
  justify-content: space-between;
  width: 100%;
`;

export const ColumnBox = styled.div`
  display: flex;
  flex-direction: column;
  width: 80%;
  margin-left: 15%;
`;

export const MainCartStyle = styled.div`
  width: 100%;
  min-height: 100vh;
  display: flex;
  /* flex-direction: column; */
  justify-content: space-between;
  padding: 20px;
  background-color: #f9f9f9;
  img {
    width: 20px;
  }
`;

export const Tablestyle = styled.table`
  width: 65%;
  background-color: #fff;
  border-radius: 15px;
  padding: 20px;
  height: fit-content;
  tr {
    th,
    td {
      text-align: left;
      font-weight: 500;
      padding: 10px 10px;
      height: 30px;
    }
    th {
      text-transform: uppercase;
      font-size: 12px;
      font-weight: bold;
    }
    td {
      border: 1px solid #aaa;
      border-left: none;
      border-right: none;
      border-collapse: collapse;
      &:first-child {
        border-left: 1px solid #ccc;
        border-radius: 7px 0 0 7px;
      }
      &:last-child {
        border-right: 1px solid #ccc;
        border-radius: 0 7px 7px 0;
      }
      img {
        width: 40px;
      }
    }
    .quantity {
      span {
        width: fit-content;
        padding: 10px;
        border-radius: 5px;
        border: 1px solid #ccc;
      }
    }
  }
`;

export const PriceBox = styled.div`
  display: flex;
  flex-direction: column;
  width: 30%;
`;

export const Price = styled.div`
  width: 100%;
  background-color: #fff;
  height: fit-content;
  padding: 0px 15px;
  border-radius: 14px;
  padding-bottom: 20px;
  h3 {
    margin-bottom: 5px;
    font-size: 20px;
    font-weight: 900;
  }
  div {
    text-align: right;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    p {
      margin: 8px;
      span {
        margin-left: 13px;
        text-align: left !important;
      }
    }
  }
`;

export const Coupon = styled.div`
  width: 100%;
  height: fit-content;
  background-color: #fff;
  padding: 10px 15px;
  border-radius: 14px;
  margin-top: 30px;
  div {
    input {
      width: calc(100% - 90px);
      height: 36px;
      text-indent: 8px;
      border: 1px solid #ccc;
      border-radius: 6px 0 0 6px;
    }
    button {
      width: 90px;
      border: 1px solid #ccc;
      height: 36px;
      border-radius: 0 6px 6px 0;
      transition-duration: 0.3s;
      &:hover {
        background-color: #bbbccc;
        color: white;
      }
    }
  }
  h3 {
    font-size: 17px;
  }
  textarea {
    width: 100%;
    height: 60px;
    border: 1px solid #ccc;
    border-radius: 6px;
    padding: 8px;
  }
`;

export const ButtonsBox = styled.div`
  width: 100%;
  display: flex;
  justify-content: flex-end;
  margin-top: 20px;
  button {
    width: 110px;
    height: 40px;
    border-radius: 8px;
    margin-left: 15px;
    color: white;
    transition-duration: 0.3s;
  }
  .clear {
    background-color: #d8514b;
    &:hover {
      background-color: #d8654f;
    }
  }
  .checkout {
    background-color: #ed774d;
    &:hover {
      background-color: #ed5f4f;
    }
  }
`;
