import React from "react";
import { Link } from "react-router-dom";
import Searchbtn from "../../assets/images/cake.jpg";
import { WrapperStyle, InputBox, ProfileBox } from "./style";

function SearchBar() {
  return (
    <WrapperStyle>
      <InputBox>
        <input type="search" placeholder="Search product" />
        <button>
          <img src={Searchbtn} alt="" />
        </button>
      </InputBox>
      <ProfileBox>
        <Link to="/cart">cart</Link>
        <div>
          <img src={Searchbtn} alt="" />
          <span>
            maxkamboy <br />
            xusniddinov
          </span>
          <img src={Searchbtn} alt="" />
        </div>
      </ProfileBox>
    </WrapperStyle>
  );
}

export default SearchBar;
