import React from "react";
import { Link } from "react-router-dom";
import { List, WrapperStyle } from "./style";

function SideBar() {
  return (
    <WrapperStyle>
      <h3>E-commerse</h3>
      <List>
        <li>
          <Link to="/">dashboard</Link>
        </li>
        <li>
          <Link to="/history">orders</Link>
        </li>
        <li>
          <Link to="/products">products</Link>
        </li>
        <li>
          <Link to="/cart">customer</Link>
        </li>
        <li>
          <Link to="/cart">message</Link>
        </li>
        <li>
          <Link to="/creation">product creation</Link>
        </li>
      </List>
    </WrapperStyle>
  );
}

export default SideBar;
