import styled from "styled-components";

export const WrapperStyle = styled.div`
  display: flex;
  justify-content: space-between;
  width: 100%;
`;

export const ColumnBox = styled.div`
  display: flex;
  flex-direction: column;
  width: 80%;
  margin-left: 15%;
`;

export const CreateBoxstyle = styled.div`
  width: 100%;
  background-color: #fff;
  margin: 20px;
  h2 {
    margin: 5px;
  }
  ul {
    display: flex;
    padding-left: 5px;
    li {
      list-style: disc;
      margin: 0 10px;
      margin-right: 20px;
    }
  }
`;

export const Shadowstyle = styled.div`
  box-shadow: 0 0 10px rgba(0, 0, 0, 0.2);
  padding: 15px 20px;
  border-radius: 15px;
  width: fit-content;
`;

export const LeftBox = styled.form`
  img {
    width: 20px;
  }
  input {
    width: 100%;
    height: 42px;
    border: 1.4px solid #ccc;
    border-radius: 6px;
    font-size: 17px;
    text-indent: 9px;
    margin-bottom: 30px;
    margin-top: 10px;
    &:focus {
      box-shadow: 0 0 10px rgba(0, 0, 0, 0.1);
    }
  }
  textarea {
    width: 100%;
    height: 120px;
    padding: 14px;
    border: 1.4px solid #ccc;
    border-radius: 6px;
    font-size: 17px;
    margin: 10px 0;
    &:focus {
      box-shadow: 0 0 10px rgba(0, 0, 0, 0.1);
    }
  }
  .selectimg {
    width: 1005;
    height: 180px;
    background-color: #f3f6f8;
    border: 2px dashed #ccc;
    border-radius: 8px;
    display: flex;
    justify-content: center;
    align-items: center;
    transition-duration: 0.3s;
    &:hover {
      box-shadow: 0 0 5px rgba(0, 0, 0, 0.2);
    }
    img {
      width: 180px;
      height: 130px;
      margin-right: 20px;
      object-fit: contain;
    }
    div {
      width: fit-content;
      h2 {
        font-size: 20px;
        font-family: Arial, Helvetica, sans-serif !important;
        font-weight: 900;
        margin: 5px;
      }
      h3 {
        font-size: 15px;
        font-weight: 900;
        margin: 5px;
        margin-top: 10px;
        color: #999;
        a {
          margin: 0 5px;
          color: #5ca3d7;
        }
      }
    }
  }
`;

export const RightTopBox = styled.div`
  width: 100%;
  box-shadow: 0 0 10px rgba(0, 0, 0, 0.2);
  padding: 15px 20px;
  border-radius: 15px;
  .title {
    display: flex;
    align-items: center;
    div {
      width: 30px;
      height: 10px;
      margin-right: 15px;
      border-radius: 4px;
      background-color: #ccc;
      position: relative;
      cursor: pointer;
      &::before {
        content: "";
        position: absolute;
        top: -2px;
        left: ${({ state }) => (state == true ? "14px" : "2px")};
        left: 14px;
        width: 15px;
        height: 15px;
        border-radius: 50%;
        background-color: green;
        z-index: 10;
      }
    }
  }
  .textinput {
    width: 100%;
    height: 42px;
    border: 1px solid #ccc;
    border-radius: 6px;
    text-indent: 8px;
    margin: 10px 0;
    &:focus {
      box-shadow: 0 0 10px rgba(0, 0, 0, 0.1);
    }
  }

  .radios {
    margin-top: 15px;
    display: flex;
    justify-content: space-evenly;
    width: 60%;
    div {
      input {
        margin-right: 8px;
        &:checked {
          background-color: #000;
        }
      }
    }
  }
  .select {
    margin: 15px 0;
  }
`;
