import styled from "styled-components";

export const WrapperStyle = styled.div`
  width: 15%;
  height: 100vh;
  position: fixed;
  top: 0;
  left: 0;
  box-shadow: 0 0 10px #eee;
  border-radius: 10px;
  h3 {
    text-align: center;
    margin: 50px auto;
    margin-bottom: 20px;
  }
`;

export const List = styled.ul`
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  li {
    padding: 18px 15px;
    text-transform: capitalize;
    width: 160px;
    margin: 10px 0;
    transition-duration: 0.3s;
    border-radius: 14px;
    &:hover {
      background-color: rgb(69, 165, 255, 0.3);
      a {
        color: rgb(69, 165, 255);
      }
    }
    a {
      color: #878787;
      font-weight: 600;
    }
    i,
    img {
      margin-right: 10px;
      width: 20px;
      height: 20px;
    }
  }
`;
