import React, { useState, useEffect } from "react";
import { BrowserRouter, Switch, Route } from "react-router-dom";
import HomePage from "../Homepage/index";
import HistoryPage from "../HistoryPage/index";
import ProductsPage from "../ProductsPage/index";
import CartPage from "../CartPage/index";
import { MainPagestyle } from "./style";
import CreatePage from "../CreatePage";

function MainPage(state) {
  return (
    <MainPagestyle>
      <BrowserRouter>
        <Switch>
          <Route path="/" exact component={HomePage} />
          <Route path="/history" component={HistoryPage} />
          <Route path="/products" component={ProductsPage} />
          <Route path="/cart" component={CartPage} />
          <Route path="/creation" component={CreatePage} />
        </Switch>
      </BrowserRouter>
    </MainPagestyle>
  );
}

export default MainPage;
