import styled from "styled-components";

export const WrapperStyle = styled.div`
  display: flex;
  justify-content: space-between;
  width: 100%;
`;

export const ColumnBox = styled.div`
  display: flex;
  flex-direction: column;
  width: 80%;
  margin-left: 15%;
  img {
    width: 20px;
  }
`;

export const Historystyle = styled.div`
  width: 100%;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  padding: 20px;
  background-color: #f9f9f9;
`;

export const Titlebox = styled.div`
  display: flex;
  justify-content: space-between;
  align-items: center;
  h2 {
    font-size: 23px;
    span {
      font-size: 15px;
      color: #8a8a8a;
    }
  }
  button {
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding: 16px /* 17px */;
    color: white;
    background-color: #45a5ff;
    border-radius: 12px;
    img {
      margin: 0 12px;
    }
    div {
      font-size: 14px;
    }
  }
`;

export const Tablestyle = styled.table`
  width: 100%;
  margin-top: 20px;
  tr {
    th,
    td {
      text-align: left;
      font-weight: 500;
      /* padding: 14px 0; */
      padding: 10px auto;
      height: 40px;
    }
    th {
      text-transform: capitalize;
      img {
        width: 12px;
      }
    }
    td {
      &:last-child{
        div{
          width: fit-content;
          height: 34px;
          padding: 5px 10px;
          border-radius: 15px;
          background-color: rgb(233,23,23,0.4);
          display: flex;
          justify-content: center;
          align-items: center;
          color: #12B357;
          background-color: rgb(18, 179, 87,0.25);
        }
      }
      img {
        margin: auto 5px;
      }
      span {
        margin:auto 5px ;
      }
    }
  }
`;
