import styled from "styled-components";

export const WrapperStyle = styled.div`
  width: 100%;
  height: fit-content;
  padding: 20px 16px;
  display: flex;
  justify-content: space-between;
  /* z-index: 10; */
  img {
    width: 20px;
  }
`;

export const InputBox = styled.div`
  width: fit-content;
  display: flex;
  align-items: center;
  input {
    width: 300px;
    height: 38px;
    border-radius: 6px 0 0 6px;
    background-color: #f9f9f9;
    color: #8a8a8a;
    border: none;
    text-indent: 10px;
    text-transform: capitalize;
  }
  button {
    width: 40px;
    height: 38px;
    display: flex;
    justify-content: center;
    align-items: center;
    background-color: #f9f9f9;
    border-radius: 0 6px 6px 0;
    img {
      width: 80%;
      border-radius: 10px;
    }
  }
`;

export const ProfileBox = styled.div`
  width: fit-content;
  display: flex;
  align-items: center;
  img {
    margin: auto 20px;
  }
  div {
    display: flex;
    align-items: center;
    img {
        width: 20px;
    }
    span {
      font-size: 14px;
      line-height: 23px;
      text-transform: capitalize;
    }
  }
`;
