import styled from "styled-components";

export const LogInWrapper = styled.div`
  display: flex;
  justify-content: center;
  align-items: center;
  min-height: 100vh;
  width: 100%;
  background-color: #2a5969;
  background-color: #353f49;
  background: rgb(103, 75, 174);
  background: linear-gradient(
    322deg,
    rgba(103, 75, 174, 1) 0%,
    rgba(107, 93, 185, 1) 47%,
    rgba(94, 144, 178, 1) 100%
  );
  form {
    width: fit-content;
    border: 1px solid #ccc;
    display: flex;
    flex-direction: column;
    padding: 20px;
    border-radius: 10px;
    background-color: rgb(255, 255, 255, 0.8);
    backdrop-filter: blur(5px);
    text-align: center;
    color: #d87647;
    h3 {
      font-size: 19px;
    }
    .error {
      margin: 5px;
      color: #a12c3b;
      display: none;
    }
    .usererror {
      display: ${(error) => (error == false ? "none" : "block")};
    }
    .passerror {
      display: ${(error) => (error == false ? "none" : "block")};
    }
    input {
      margin: 14px auto;
      width: 300px;
      height: 40px;
      border: 1px solid #ccc;
      border-radius: 6px;
      text-indent: 6px;
      color: #d87647;
      &:focus {
        box-shadow: 0 0 5px rgba(0, 0, 0, 0.2);
      }
    }
    button {
      width: fit-content;
      margin: 10px auto;
      padding: 15px 30px;
      text-transform: uppercase;
      transition-duration: 0.3s;
      background: rgb(103, 75, 174);
      background: linear-gradient(
        322deg,
        rgba(103, 75, 224, 1) 0%,
        rgba(107, 93, 185, 1) 47%,
        rgba(94, 144, 178, 1) 100%
      );
      border-radius: 8px;
      transition-duration: 0.3s;
      color: white;
      i{
        margin-right: 8px;
        font-size: 17px;
      }
      &:hover {
        background-color: #2a5969;
        background: rgb(103, 75, 174);
        background: linear-gradient(
          92deg,
          rgba(103, 75, 171, 1) 0%,
          rgba(107, 93, 185, 1) 47%,
          rgba(94, 144, 178, 1) 100%
        );
        box-shadow: 0 0 8px rgba(0, 0, 0, 0.7);
      }
    }
  }
`;

export const LogOutBtn = styled.button`
  position: fixed;
  top: 20px;
  left: 20px;
  font-size: 20px;
  background-color: #2a5969;
  background: rgb(103, 75, 174);
  background: linear-gradient(
    322deg,
    rgba(103, 75, 174, 1) 0%,
    rgba(107, 93, 185, 1) 47%,
    rgba(94, 144, 178, 1) 100%
  );
  border-radius: 10px;
  padding: 4px 10px;
  color: white;
  cursor: pointer;
  z-index: 100;
  transition-duration: 0.3s;
  &:hover {
    box-shadow: 0 0 5px rgba(0, 0, 0, 0.4);
    background: rgb(103, 75, 174);
    background: linear-gradient(
      92deg,
      rgba(103, 75, 174, 1) 0%,
      rgba(107, 93, 185, 1) 47%,
      rgba(94, 144, 178, 1) 100%
    );
  }
`;
